import React, { useEffect } from "react";
import useHttp from "../../../../hooks/use-http";
import { getMyOrders } from "../../../../lib/orders-api";
import MyOrdersList from "./MyOrdersList";

const MyOrders = (props) => {
  const { sendRequest, data: loadedOrders } = useHttp(getMyOrders);

  useEffect(() => {
    sendRequest();
  }, [sendRequest]);
  console.log("b", loadedOrders);

//   const orders = loadedOrders;

  const ordersList = loadedOrders.map((orders) => (
    <MyOrdersList
      key={orders.key}
      id={orders.key}
      orderTotalPrice={orders.orderTotalPrice}
      orderTotalQty={orders.orderTotalQty}
    />
  ));
  
  console.log("c", ordersList);

  return 
//   <div>{ordersList}</div>;
};

export default MyOrders;
