const FIREBASE_ORDERS_DOMAIN =
  "https://sl-rc-store-default-rtdb.asia-southeast1.firebasedatabase.app/orders";

const userId = localStorage.getItem("localId");

export async function getMyOrders() {
  const response = await fetch(`${FIREBASE_ORDERS_DOMAIN}/${userId}.json`);
  const data = await response.json();

  if (!response.ok) {
    throw new Error(data.message || "Could not fetch your orders.");
  }

  const transformedOrders = [];

  for (const key in data) {
    const orderObj = {
      id: key,
      ...data[key],
    };
    transformedOrders.push(orderObj);
  }

  console.log("a", data);
  return transformedOrders;
}
